import Vue from '@/main.js'
import store from '@/store'
import {is_json} from "@/libs/tools";

export const analyzeResponse = (res) => {
    let data = res.data;
    return is_json(data) ? JSON.parse(data) : data;
};

export const notification = (res) => {
    //TODO 判断res返回值success
    console.log('notification', res);

    switch (res.key) {
        case 'set_uid':
            Vue.$notification.success({
                duration: 1,
                message: '推送服务连接成功',
            });
            break;
        case 'push_notification_to_uid':
            store.state.pc.pushSignal++;
            const key = `open${Date.now()}`;
            Vue.$notification.info({
                key,
                duration: null,
                message: '新的通知',
                btn: (h) => {
                    return h('a-button', {
                        props: {
                            type: 'primary',
                            size: 'small',
                        },
                        on: {
                            click: () => {
                                Vue.$router.push({name: '通知列表', params: {type: 0}});
                                Vue.$notification.close(key);
                            }
                        }
                    }, '去查看')
                },
            });
            break;
        case 'push_to_uid':
            Vue.$notification.info({
                duration: null,
                message: '新的通知',
            });
            break;
        case 'push_to_all':
            Vue.$notification.info({
                duration: null,
                message: '新的通知',
            });
            break;
        case 'push_to_admin':
            Vue.$notification.warning({
                duration: null,
                message: '新的通知',
            });
            break;
        default:
            console.log(res);
            break;
    }
}

