import Vue from '@/main.js'
import store from '@/store'
import {analyzeResponse, notification, chat} from './helper'

class socket {
    url = store.state.common.websocketBaseUrl;
    _heart_beat_time = 30000;
    _interval_id = null;
    _ws = null;
    _event = null;

    set ws(val) {
        this._ws = val;
    }

    get ws() {
        return this._ws;
    }

    set event(val) {
        this._event = val;
    }

    get event() {
        return this._event;
    }

    get isOnline() {
        return !!this.ws;
    }

    constructor(event, uid) {
        this.event = event;
        this.ws = new WebSocket(this.url);

        let _self = this;

        this.ws.onopen = function (evt) {
            //心跳
            clearInterval(_self._interval_id);

            _self._interval_id = setInterval(() => {
                _self.send(_self.event, 'beep', 1);
            }, _self._heart_beat_time);

            _self.send(_self.event, 'register',uid);
        };

        this.ws.onmessage = (res) => {
            console.log('socket init', analyzeResponse(res));
        };

        this.ws.onclose = function (evt) {
            clearInterval(_self._interval_id);
        };
    }

    send(event, requestKey, requestValue, usePromise = false) {
        if (!this.isOnline) {
            return false;
        }

        if (usePromise) {
            let _self = this;
            return new Promise(function (resolve, reject) {
                _self.ws.onmessage = (res) => {
                    resolve(analyzeResponse(res));
                };
                _self.ws.send(JSON.stringify({event, key: requestKey, value: requestValue}));
            });
        } else {
            this.ws.send(JSON.stringify({event, key: requestKey, value: requestValue}));
        }
    }

    onmessage(func){
        this.ws.onmessage = (res) => {
            func(analyzeResponse(res));
        };
    }

    close(){
        clearInterval(this._interval_id);
        this.ws.close();
    }
}

export default socket;
