<template>
    <div class="chat-card">
        <header>
            <template v-if="!isOnline">
                <a-alert message="连接已断开" banner @click.native="getOnline" style="cursor:pointer"/>
            </template>
            <template v-else>
                <img class="avatar"
                     width="40"
                     height="40"
                     :src="thumbnail">
                <p class="name">{{user.name}}</p>
            </template>
        </header>
        <footer>
            <input class="search"
                   type="text"
                   placeholder="search user...">
        </footer>
    </div>
</template>

<script>
    import {getImageUrl} from "@/libs/tools";

    export default {
        props: {
            user: {
                required: true,
                type: Object,
            },
            isOnline:{
                required: true,
                type:Boolean,
            },
            ws:{
                required: true,
                type: Object,
            }
        },
        computed: {
            thumbnail: {
                get: function () {
                    return this.user.hasOwnProperty('more')
                        ? getImageUrl(this.user.more.thumbnail)
                        : null;
                }
            }
        },
        data() {
            return {}
        },
        methods:{
            getOnline(){
                this.$emit('getOnline');
            }
        }
    };
</script>

