<template>
    <div class="chat-message" id="chat-message">
        <ul>
            <li v-for="item in messages" :key="item.id">
                <p class="time">
                   <span>{{getDate(item.created_at)}}</span>
                </p>
                <div class="main" :class="{ self: item.from_user_id===user.id }">
                    <img class="avatar" width="30" height="30" :src="getHeadImg(item)"/>
                    <div class="text">{{ item.message }}</div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    import {getDate, getImageUrl} from '@/libs/tools'

    export default {
        props: {
            isOnline: {
                required: true,
                type: Boolean
            },
            ws: {
                required: true,
                type: Object,
            },
            user: {
                required: true,
                type: Object
            },
            session_user: {
                required: true,
                type: Object,
            }
        },
        data() {
            return {
                messages: []
            }
        },
        methods: {
            getDate(t) {
                return getDate(t);
            },
            getHeadImg(item) {
                let url = item.from_user_id === this.user.id
                    ? this.user.thumbnail_format
                    : this.session_user.thumbnail_format;
                return getImageUrl(url);
            },
            setMessage(data) {
                this.messages = data;
                this.scrollDiv();
            },
            appendMessage(data) {
                this.messages.push(data);
                this.scrollDiv();
            },
            scrollDiv() {
                this.$nextTick(function () {
                    var div = document.getElementById('chat-message');
                    div.scrollTop = div.scrollHeight;
                })
            }
        },
        watch: {
            session_user: {
                handler: function (val, oldVal) {
                    if ((val.hasOwnProperty('id') && oldVal.hasOwnProperty('id')) || (!val.hasOwnProperty('id') && oldVal.hasOwnProperty('id'))) {
                        this.messages = [];
                    }
                },
                deep: true
            },
        }
    };
</script>

