<template>
    <div class="text">
        <textarea
                placeholder="按 Ctrl + Enter 发送"
                v-if="session_user_id"
                v-model="content"
                @keyup="onKeyup"></textarea>
    </div>
</template>

<script>
    export default {
        props: {
            isOnline: {
                required: true,
                type: Boolean
            },
            ws: {
                required: true,
                type: Object,
            },
            user_id: {
                required: true,
                type: Number
            },
            session_user_id: {
                required: true,
                type: Number,
            }
        },
        data() {
            return {
                content: ''
            };
        },
        methods: {
            handleSendMessage({type, message}) {
                let sendData = {from_user_id: this.user_id, to_user_id: this.session_user_id, type, message};
                this.ws.send('chat', 'chat_send_message_to_user', sendData);
            },
            onKeyup(e) {
                if (!this.isOnline) {
                    this.$message.warning('连接已断开');
                    return false;
                }

                if (!this.session_user_id) {
                    return false;
                }

                if (e.ctrlKey && e.keyCode === 13 && this.content.length) {
                    this.handleSendMessage({type: 1, message: this.content});
                    this.content = '';
                }
            }
        }
    };
</script>

<style lang="less" scoped>

    .text {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        height: 160px;
        border-top: solid 1px #ddd;

        textarea {
            padding: 10px;
            height: 100%;
            width: 100%;
            border: none;
            outline: none;
            font-family: "Micrsofot Yahei";
            resize: none;
        }
    }
</style>