<template>
    <div id="chat">
        <div class="sidebar">
            <card-component
                    :user="user"
                    :isOnline="isOnline"
                    :ws="WsObj"
                    @getOnline="getOnline"></card-component>
            <list-component
                    :users="chat_users"
                    :user_id="user_id"
                    :isOnline="isOnline"
                    :ws="WsObj"
                    @setSession="setSession"
                    :session_user_id="session_user_id"></list-component>
        </div>
        <div class="main">
            <message-component
                    ref="message"
                    :isOnline="isOnline"
                    :ws="WsObj"
                    :user="user"
                    :session_user="session_user"></message-component>
            <text-component
                    :isOnline="isOnline"
                    :ws="WsObj"
                    :user_id="user_id"
                    :session_user_id="session_user_id"></text-component>
        </div>
    </div>
</template>

<script>
    import CardComponent from './components/Card';
    import ListComponent from './components/List';
    import TextComponent from './components/Text';
    import MessageComponent from './components/Message';
    import ws from '@/socket'
    import {getUser, getDepartmentUserList} from '@/api/user'
    import {in_array} from '@/libs/tools'

    export default {
        components: {CardComponent, ListComponent, TextComponent, MessageComponent},
        data() {
            return {
                user_id: parseInt(this.$store.state.common.user_id),
                user: {},
                online_user_ids: [],
                chat_users: [],
                session_user: {},
                WsObj: {},
            }
        },
        computed: {
            isOnline() {
                return !!this.WsObj.isOnline;
            },
            session_user_id() {
                if (this.session_user.hasOwnProperty('id')) {
                    return this.session_user.id;
                } else {
                    return 0;
                }
            }
        },
        methods: {
            init() {
                this.WsObj = new ws('chat', this.user_id);
                this.WsObj.onmessage(this.watchSocket);

                getUser()
                    .then(res => {
                        this.user = res.data;
                    })

                getDepartmentUserList({current_page: 0, filter: {}})
                    .then(res => {
                        let rows = res.data.rows;
                        for (let i = 0; i < rows.length; i++) {
                            rows[i].online_status = 0;
                        }

                        if (this.online_user_ids.length) {
                            for (let i = 0; i < rows.length; i++) {
                                let user_id = rows[i].id;
                                if (in_array(user_id, this.online_user_ids)) {
                                    rows[i].online_status = 1;
                                }
                            }
                        }

                        this.chat_users = rows;
                    })
            },
            watchSocket(res) {
                if (!res.success) {
                    return false;
                }

                if (!res.hasOwnProperty('data') || !res.data) {
                    console.log('res data null');
                    return false;
                }

                if (res.event !== 'chat' || !res.data.hasOwnProperty('key')) {
                    return false;
                }
                let data = res.data;
                let key = data.key;

                switch (key) {
                    case "chat_chat_users_status":
                        this.online_user_ids = data.uids;
                        if (this.chat_users.length) {
                            for (let i = 0; i < this.chat_users.length; i++) {
                                let user_id = this.chat_users[i].id;
                                if (in_array(user_id, this.online_user_ids)) {
                                    this.$set(this.chat_users[i], 'online_status', 1);
                                } else {
                                    this.$set(this.chat_users[i], 'online_status', 0);
                                }
                            }
                        }
                        break;
                    case "chat_new_message":
                        if (data.from_user_id === this.session_user_id) {
                            this.$refs.message.appendMessage(data);
                        } else {
                            this.appendListBadge(data.from_user_id);
                        }
                        break;
                    case "chat_send_feedback":
                        this.$refs.message.appendMessage(data);
                        break;
                    case "chat_user_get_online":
                        let user_id = data.user_id;
                        this.handleUserGetOnline(user_id);

                        break;
                    case "chat_user_get_offline":
                        user_id = data.user_id;
                        this.handleUserGetOffline(user_id);
                        break;
                    case "chat_set_session_uid":
                        let rows = data.rows;
                        this.$refs.message.setMessage(rows);
                        break;
                }
            },
            appendListBadge(user_id) {
                for (let i = 0; i < this.chat_users.length; i++) {
                    if (this.chat_users[i].id === user_id) {
                        this.$set(this.chat_users[i], 'has_new_message', 1);
                        return true;
                    }
                }
            },
            handleUserGetOnline(user_id) {
                for (let i = 0; i < this.chat_users.length; i++) {
                    if (this.chat_users[i].id === user_id) {
                        this.$set(this.chat_users[i], 'online_status', 1);
                        this.chat_users.unshift(this.chat_users.splice(i, 1)[0]);
                        return true;
                    }
                }
            },
            handleUserGetOffline(user_id) {
                for (let i = 0; i < this.chat_users.length; i++) {
                    if (this.chat_users[i].id === user_id) {
                        this.$set(this.chat_users[i], 'online_status', 0);
                        return true;
                    }
                }
            },
            getOnline() {
                let _self = this;
                this.$confirm({
                    title: '操作确认',
                    content: '要重新连接推送服务器吗？',
                    onOk() {
                        _self.init();
                    },
                    onCancel() {
                        console.log('Cancel');
                    },
                    class: 'test',
                });
            },
            setSession(session_user_id) {
                this.session_user = {};

                let sendData = {from_user_id: this.user_id, session_user_id};
                this.WsObj.send('chat', 'chat_set_session_uid', sendData);

                getUser(session_user_id)
                    .then(res => {
                        this.session_user = res.data

                        for (let i = 0; i < this.chat_users.length; i++) {
                            if (this.chat_users[i].id === session_user_id) {
                                this.$set(this.chat_users[i], 'has_new_message', 0);
                                return true;
                            }
                        }
                    })
            },
        },
        mounted() {
            // this.init();
        },
        beforeDestroy() {
            if (this.isOnline) {
                this.WsObj.close();
            }
        }
    }
</script>

<style lang="less" scoped>
    #chat {
        margin: 20px auto;
        width: 800px;
        height: 600px;

        overflow: hidden;
        border-radius: 3px;

        .sidebar, .main {
            height: 100%;
        }
        .sidebar {
            float: left;
            width: 200px;
            color: #f4f4f4;
            background-color: #2e3238;
        }
        .main {
            position: relative;
            overflow: hidden;
            background-color: #eee;
        }
    }
</style>
